import { LoginForm, ProFormText } from "@ant-design/pro-form";
import { UserOutlined } from "@ant-design/icons";
import { Button, message } from "antd";
import { Link } from "react-router-dom";
import style from "./index.module.scss";
import { useState } from "react";

const waitTime = (time = 100) => {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve(true);
    }, time);
  });
};

const CheckEmailDom = () => {
  return (
    <>
      <div className={style.contentBox}>
        <h3>Check Your Email</h3>
        <p>
          We have send you a verification link to{" "}
          <a href="mailto:xyz123@gmail.com">xyz123@gmail.com</a>.
        </p>
        <p>Please click the link in that meaage to active your account.</p>
        <h6>
          Return to <Link to="login">Sign in</Link>
        </h6>
      </div>
    </>
  );
};

const ForgotPassDom = ({ submitEmail }) => {
  return (
    <div className={style.boxPos}>
      <LoginForm
        title="Forgot Password"
        onFinish={async (values) => {
          await waitTime(200);
          console.log(values);
          message.success("success");
        }}
        submitter={{
          // 完全自定义整个区域
          render: (props, doms) => {
            return [
              <Button
                className={style.signButton}
                block
                type="primary"
                onClick={() => submitEmail(props)}
              >
                Submit
              </Button>,
            ];
          },
        }}
        actions={
          <div>
            <div className={style.textCenter}>
              Already have an account?<Link to="login">Sign in</Link>
            </div>
            <div className={style.textCenter}>
              New to Kmind? <Link to="create-account">Create Account</Link>
            </div>
          </div>
        }
      >
        {
          <>
            <ProFormText
              name="username"
              label="E-mail"
              fieldProps={{
                size: "large",
                prefix: <UserOutlined className={"prefixIcon"} />,
              }}
              placeholder={"please input email"}
              rules={[
                {
                  required: true,
                  message: "please input email!",
                },
              ]}
            />
          </>
        }
      </LoginForm>
    </div>
  );
};

const ForgotPass = () => {
  const [isSuccEmail, setIsSuccEmail] = useState(false);
  const submitEmail = (props) => {
    props.form?.submit?.();
    setIsSuccEmail(true);
  };

  return <>{isSuccEmail ? CheckEmailDom() : ForgotPassDom({ submitEmail })}</>;
};

export default ForgotPass;
