import { HashRouter as Router, Route } from "react-router-dom";
import routerList from "./router/index";
import Header from "./components/Header";
function App() {
  return (
    <Router>
      <div style={{ minWidth: "1200px", maxWidth: "1600px", margin: "0 auto" }}>
        <Header></Header>
        {routerList.map((item) => (
          <Route key={item.path} path={item.path} exact component={item.page} />
        ))}
      </div>
    </Router>
  );
}

export default App;
