import style from "./index.module.scss";
// import { useActions, useThunkLoading, useSelector } from "cosmos-redux";
// import { actions, thunks, selectors } from "./model";
// import { useEffect } from "react";
import { Menu, Button, Dropdown } from "antd";
import {
  UnorderedListOutlined,
  MessageOutlined,
  DownOutlined,
} from "@ant-design/icons";
import { useHistory, useLocation } from "react-router-dom";
import { menus } from "./config";
import { useEffect, useState } from "react";

function Header() {
  const [activeKey, setActiveKey] = useState("");
  // const actionMap = useActions(actions, thunks);
  // const loading = useThunkLoading(thunks.getTodoList);
  // const todoList = useSelector(selectors.todoList);
  // useEffect(() => {
  //   actionMap.getTodoList();
  // }, [actionMap]);
  const history = useHistory();
  const location = useLocation();
  const menuSelect = (obj) => {
    history.push(obj.key);
  };
  useEffect(() => {
    setActiveKey(location.pathname);
  }, [location]);
  return (
    <div className={style.header}>
      <div className={style.box}>
        {/* 1{loading.toString()}
        {todoList} */}
        <img
          className={style.logo}
          src={process.env.PUBLIC_URL + "/Kmind-logo.png"}
          alt=""
        />
        <Menu
          selectedKeys={[activeKey]}
          mode="horizontal"
          className={style.menu}
          onSelect={menuSelect}
        >
          {menus.map((item) => (
            <Menu.Item className={style.menuItem} key={item.path}>
              {item.name}
            </Menu.Item>
          ))}
        </Menu>
        {/* courses */}
        <Button
          onClick={() => menuSelect({ key: "my-courses" })}
          type="text"
          className={style.courses}
        >
          <UnorderedListOutlined style={{ fontSize: "28px" }} />
          <span>My Courses</span>
        </Button>
        {/* message */}
        <Button type="text" className={style.courses}>
          <MessageOutlined style={{ fontSize: "28px" }} />
          <span>Message</span>
        </Button>
        {/* sign in */}
        <Dropdown
          overlay={
            <Menu>
              {menus.map((item) => (
                <Menu.Item className={style.menuItem} key={item.key}>
                  {item.name}
                </Menu.Item>
              ))}
            </Menu>
          }
          trigger={["click"]}
          className={style.rightMenu}
        >
          <Button
            type="link"
            className="ant-dropdown-link"
            onClick={(e) => e.preventDefault()}
          >
            Sign in <DownOutlined />
          </Button>
        </Dropdown>
      </div>
    </div>
  );
}

export default Header;
